@media screen and (max-width: 1200px) {
    .footer {
        padding: 30px 0;
    }
    .footer-item {
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width: 992px) {
    header.menu nav a {
        font-size: 0.7rem;
        padding: 0 2px;
    }
    .promo {
        margin: 65px auto 0;
    }
    .logo-img {
        max-width: 225px;
    }
    .hero .logo {
        text-align: center;
        padding: 10px 0 25px 0;
    }
    .hero .contact {
        justify-content: center;
        padding: 0 0 10px 0;
    }
    a.main-contact-number {
        font-size: 1rem;
    }
    .choice-background {
        width: 400px;
    }
    .choice-img {
        width: 550px;
        bottom: -160px;
    }
    .button-blue-large {
        margin: 30px auto 60px;
    }
    .slogan {
        padding: 150px 0 52px;
    }
}

@media screen and (max-width: 768px) {
    .promo p {
        display: none;
    }
    button.button-blue-large {
        margin: 20px auto;
    }
    section.hero {
        min-height: 600px;
    }
}
