@use "media";

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
@import url("https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/css/bootstrap.min.css");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.1/font/bootstrap-icons.css");

$primary-color: #1eacc7;

* {
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    margin: 0;
    padding: 0;
}

html {
    font-size: 16px;
}

@mixin titles {
    font-size: 2.1rem;
    font-weight: 700;
    text-transform: uppercase;
    color: #000000;
}

h2.title {
    @include titles;
}

h2.title-white {
    @include titles;
    color: #f8f8f8;
}

@mixin subtitle {
    font-size: 1rem;
    margin: 0 0 35px 0;
    color: #000000;
}

h4.subtitle-black {
    @include subtitle;
}

h4.subtitle-white {
    @include subtitle;
    color: #f8f8f8;
}

@mixin label {
    display: inline-block;
    padding: 6px 12px;
    font-size: 0.75rem;
    text-transform: uppercase;
    text-align: center;
    margin: 0 0 30px 0;
    transition: all 0.25s;

    &:hover {
        transform: rotate(-2deg) scale(1.05, 1.05);
    }
}

h2.label-black {
    @include label;
    background-color: #000000;
    color: #f8f8f8;
}

h2.label-white {
    @include label;
    background-color: #ffffff;
    color: #000000;
}

@mixin button-blue {
    width: 172px;
    height: 45px;
    font-size: 0.85rem;
    border-radius: 0.35rem;
    border: 0;
    background-color: #23b2cd;
    background-image: linear-gradient(to top, #23b2cd 0%, #48ddfa 100%);
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.15);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
    text-transform: uppercase;
    color: #f8f8f8;
    cursor: pointer;
    outline: none;
    transition: all 0.25s;
    &:hover {
        box-shadow: 0 -4px 0 0 rgba(0, 0, 0, 0.25) inset;
        background-image: none;
    }
    &:active {
        box-shadow: 0 3px 5px 2px rgba(0, 0, 0, 0.5) inset;
    }
}

.button-blue-large {
    @include button-blue;
    font-size: 1.125rem;
    width: 260px;
    height: 65px;
    margin: 115px auto 30px;
}

.button-blue-small {
    @include button-blue;
}

header {
    min-height: 50px;
    background-color: #212121;
    nav {
        padding: 0 30px;
        ul {
            list-style: none;
            padding: 10px 0;
            margin-bottom: 0;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
        li.menu-item {
            display: inline;
            line-height: 30px;
        }
        a {
            color: #dadada;
            text-decoration: none;
            font-size: 0.92rem;
            font-weight: 300;
            padding: 0 8px;
        }
        li.divider {
            border-right: 2px dashed #000000;
        }
    }
}

.hero {
    min-height: 800px;
    background: #333333 url("../img/car_bg.jpg") center/cover no-repeat;
    &-header {
        min-height: 80px;
        background-color: rgba(0, 0, 0, 0.6);
        padding: 15px 0;
    }
    .logo {
        text-align: left;
        padding: 0 0 0 5rem;
        img {
            max-width: 285px;
        }
        h2 {
            color: #dadada;
            font-weight: 100;
            font-size: 0.825rem;
            padding: 5px 0;
        }
    }

    .contact {
        display: flex;
        justify-content: right;
        padding: 0 5rem 0 0;
        &-text {
            text-align: right;
            margin: 5px 25px 0 0;
        }
        h3 {
            color: #dadada;
            font-size: 0.825rem;
            font-weight: 100;
            margin-bottom: 0;
        }
        a {
            font-size: 1.5rem;
            color: #dadada;
            text-decoration: none;
        }
    }
}

.promo {
    margin: 65px auto;
    text-align: center;
    h1 {
        font-size: 3.5rem;
        font-weight: 700;
        text-transform: uppercase;
        color: #f8f8f8;
        margin-bottom: 0;
    }
    &-subtitle {
        font-size: 2.1rem;
        font-weight: 700;
        text-transform: uppercase;
        color: #f8f8f8;
        margin: 0 0 20px 0;
    }
    p {
        color: #f8f8f8;
        font-size: 1.1rem;
    }
}

.reasons {
    padding: 70px 0 60px 0;
    background-color: #ffffff;
    text-align: center;
    &-item {
        padding: 40px 40px 0 40px;
        display: flex;
    }
    &-image {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 112px;
        height: 112px;
        background-color: $primary-color;
        border-radius: 50%;
        margin: 10px 25px 10px 0;
        transition: all 0.25s;
        &:hover {
            transform: scale(1.05, 1.05);
        }
    }
    h3 {
        font-size: 1.1rem;
        color: $primary-color;
        text-align: left;
    }
    p {
        font-size: 1rem;
        line-height: 1.25rem;
        font-weight: 300;
        text-align: justify;
    }
}

.app {
    background: #f8f8f8 url("../img/tile_bg.jpg") repeat;
    padding: 45px 0;
    text-align: center;
    &-items {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    &-item {
        position: relative;
        display: flex;
        justify-content: center;
        width: 320px;
        height: 280px;
        margin: 10px;
    }
    &-shadow-block {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(32, 30, 51, 0.85);
        z-index: 1;
        text-align: center;
        padding: 90px 10px 10px 10px;
    }
    h3 {
        text-transform: uppercase;
        color: #ffffff;
        font-size: 1.5rem;
        line-height: 2rem;
    }
    &-icon-plus {
        width: 26px;
        height: 26px;
        display: inline-block;
        position: absolute;
        z-index: 2;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 50%;
        background-color: #000000;
        color: $primary-color;
        font-size: 1.6rem;
        line-height: 26px;
        font-weight: 300;
    }
}

.choice {
    padding: 2rem 0 0 0;
    background-color: #000000;
    text-align: center;
    position: relative;

    &-background {
        margin-top: 20px;
    }

    &-img {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        bottom: -215px;
    }
}

.slogan {
    background: #f8f8f8 url("../img/tile_bg.jpg") repeat;
    text-align: center;
    padding: 235px 0 111px;
    p {
        line-height: 0.6rem;
        font-size: 1.05rem;
    }
}

.rules {
    text-align: center;
    padding: 50px 0 30px;
    h3 {
        font-size: 1.5rem;
        margin-bottom: 20px;

        .blue {
            color: $primary-color;
        }
        .coral {
            color: #ff5556;
        }
    }
    &-item {
        margin: 20px 0 30px 0;
        text-align: left;
    }
    ul {
        list-style: none;
        margin: 0;
        li {
            font-size: 1.05rem;
            padding: 0 0 8px 30px;
            line-height: 1.4rem;
        }
        .check-blue {
            background: url("../img/check-blue.svg") left top no-repeat;
        }
        .check-coral {
            background: url("../img/check-coral.svg") left top no-repeat;
        }
    }
    &-highlight {
        color: $primary-color;
        margin: 0;
        font-size: 1.07rem;
    }
}

.map {
    background-color: #070a11;
    text-align: center;
    .container {
        position: relative;
        min-height: 650px;
        padding: 50px 0;
        background: url("../img/world_map_bg.jpg") center/cover no-repeat;
    }
    &-link {
        position: absolute;
        display: block;
        text-transform: uppercase;
        color: $primary-color;
        text-decoration: none;
        left: 50%;
        bottom: 50px;
        transform: translateX(-50%);
        transition: all 0.5s;
        &:hover {
            color: #ff5556;
        }
    }
}

.footer {
    &-items {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
    }
    ul.social-links {
        list-style: none;
        display: flex;
        padding: 0;
        li {
            margin: 0 5px;
            a {
                display: inline-block;
                background-color: #212121;
                text-align: center;
                vertical-align: middle;
                border-radius: 50%;
                width: 32px;
                line-height: 32px;
                box-shadow: 0 3px 4px rgba(0, 0, 0, 0.15);
                transition: all 0.25s;
                &:hover {
                    transform: scale(1.1, 1.1);
                }
                i {
                    color: #ffffff;
                    font-size: 0.9rem;
                }
            }
        }
    }
    .site-links {
        text-align: center;
        .divider-blue {
            background-color: $primary-color;
            width: 300px;
            height: 4px;
            margin: 15px auto 35px;
        }
        ul {
            list-style: none;
            display: flex;
            justify-content: center;
            margin: 0;
            padding: 0;
            li {
                display: inline-block;
                a {
                    text-transform: uppercase;
                    text-decoration: none;
                    color: #000000;
                    &:hover {
                        color: $primary-color;
                    }
                }
            }
        }
        &-large {
            li {
                margin: 0 20px;
            }
        }
        &-small {
            li {
                margin: 5px 15px;
                a {
                    color: #a6a6a6;
                    font-size: 0.7rem;
                }
            }
        }
        &-lang {
            display: inline-block;
            margin: 15px 0 30px;
            text-transform: uppercase;
            text-decoration: none;
            color: $primary-color;
            transition: all 0.5s;
            &:hover {
                color: #ff5556;
            }
        }
    }
    ul.app-links {
        list-style: none;
        margin: 0 20px;
        padding: 0;
        display: flex;
        justify-content: end;
        align-items: center;
        li {
            margin: 0 6px;
            a {
                img {
                    height: 30px;
                    transition: all 0.25s;
                    &:hover {
                        transform: scale(1.05, 1.05);
                    }
                }
            }
        }
    }
}
